.job-item-details-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #000000;
    min-height: 100vh;
  }
  .job-item-loader-container {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
  }
  @media screen and (min-width: 768px) {
    .job-item-loader-container {
      width: 100%;
    }
  }
  .job-item-error-view-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
  @media screen and (min-width: 768px) {
    .job-item-error-view-container {
      width: 100%;
      margin-top: 70px;
      padding-bottom: 0px;
    }
  }
  .job-item-failure-img {
    width: 250px;
    height: 200px;
  }
  @media screen and (min-width: 768px) {
    .job-item-failure-img {
      width: 450px;
      height: 400px;
    }
  }
  .job-item-failure-heading-text {
    color: #ffffff;
    font-family: 'Roboto';
    font-size: 24px;
    font-weight: 500;
    line-height: 1.3;
    text-align: center;
  }
  @media screen and (min-width: 768px) {
    .job-item-failure-heading-text {
      font-size: 38px;
    }
  }
  .job-item-failure-description {
    text-align: center;
    color: #ffffff;
    font-family: 'Roboto';
    font-size: 12px;
    width: 90%;
    max-width: 488px;
    margin-top: 0px;
    line-height: 1.8;
  }
  .job-item-failure-button {
    padding: 8px 32px;
    border-radius: 5px;
    border: none;
    outline: none;
    cursor: pointer;
    background-color: #4f46e5;
    color: #ffffff;
    font-size: 18px;
    font-weight: 500;
    font-family: 'Roboto';
  }
  .job-details-view-container {
    display: flex;
    flex-direction: column;
    width: 90%;
    max-width: 1180px;
    padding-top: 20px;
  }
  .job-item {
    display: flex;
    flex-direction: column;
    background-color: #202020;
    /* background-color: white; */
    padding: 25px;
    border-radius: 15px;
    margin-bottom: 25px;
    margin-top: 10px;
  }
  @media screen and (min-width: 768px) {
    .job-item {
      padding: 45px;
    }
  }
  .logo-title-location-container {
    display: flex;
    flex-direction: column;
    /* background-color: white; */
  }
  .logo-title-container {
    display: flex;
    align-items: center;
  }
  .company-logo {
    width: 70px;
    height: 70px;
    margin-right: 20px;
  }
  .title-rating-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .title-heading {
    color: #ffffff;
    font-size: 20px;
    font-weight: bold;
    font-family: 'Roboto';
    margin-bottom: 0px;
  }
  .rating-container {
    display: flex;
    align-items: center;
    padding-top: 0px;
  }
  .rating-icon {
    color: #fbbf24;
    font-size: 18px;
    margin-right: 10px;
  }
  .rating-heading {
    color: #ffffff;
    font-size: 18px;
    font-weight: bold;
  
    font-family: 'Roboto';
  }
  
  .location-package-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    /* background-color: white; */
  }
  .location-employee-container {
    display: flex;
    flex-direction: row;
  }
  .location-container {
    display: flex;
    align-items: center;
    margin-right: 10px;
    padding-left: 5px;
  }
  .location-icon {
    color: #ffffff;
    font-size: 22px;
    margin-right: 6px;
  }
  .brief-case-icon {
    color: #ffffff;
    font-size: 22px;
    margin-right: 10px;
  }
  .location-heading {
    color: #ffffff;
    font-size: 14px;
    font-family: 'Roboto';
  }
  .package-heading {
    color: #ffffff;
    font-size: 18px;
    font-family: 'Roboto';
    font-weight: 500;
  }
  .employee-type-container {
    display: flex;
    align-items: center;
    padding-left: 5px;
  }
  .employee-type-heading {
    color: #ffffff;
    font-size: 14px;
    font-family: 'Roboto';
  }
  .description-visit-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .visit-container {
    display: flex;
    align-items: center;
  }
  .visit-heading {
    color: #6a95ce;
    font-size: 14px;
    font-family: 'Roboto';
    text-decoration: none;
    padding-right: 4px;
  }
  .visit-icon {
    color: #6a95ce;
    font-size: 14px;
    font-family: 'Roboto';
  }
  .line {
    border-color: #6a95ce;
    width: 100%;
  }
  .description-heading {
    color: #ffffff;
    font-size: 22px;
    font-weight: bold;
    font-family: 'Roboto';
  }
  .description-text {
    color: #ffffff;
    font-size: 15px;
    line-height: 1.8;
    font-family: 'Roboto';
  }
  
  .similar-jobs-heading {
    color: #ffffff;
    font-family: 'Roboto';
    font-size: 28px;
    font-weight: 500;
    margin: 0;
  }
  
  @media screen and (min-width: 768px) {
    .similar-jobs-heading {
      font-size: 32px;
    }
  }
  
  .similar-jobs-list {
    display: flex;
    flex-direction: column;
    padding-left: 0;
  }
  
  @media screen and (min-width: 768px) {
    .similar-jobs-list {
      display: flex;
      flex-direction: row;
      margin-top: 24px;
    }
  }
  .skills-heading {
    color: #ffffff;
    font-size: 22px;
    font-weight: bold;
    font-family: 'Roboto';
  }
  .life-at-company-heading {
    color: #ffffff;
    font-size: 22px;
    font-weight: bold;
    font-family: 'Roboto';
  }
  
  .life-at-company-description-image-container {
    display: flex;
    flex-direction: column;
  }
  .life-at-company-description {
    color: #ffffff;
    font-size: 15px;
    line-height: 1.8;
    font-family: 'Roboto';
  }
  .life-at-company-image {
    width: 100%;
  }
  @media screen and (min-width: 768px) {
    .life-at-company-description-image-container {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
    }
    .life-at-company-description {
      font-size: 20px;
      width: 65%;
    }
    .life-at-company-image {
      width: 350px;
      height: 260px;
    }
    .description-text {
      font-size: 20px;
    }
  }
  .skills-list-container {
    display: flex;
    flex-wrap: wrap;
    list-style-type: none;
    padding-left: 0px;
  }
  .skills-item-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 50%;
    margin-bottom: 40px;
  }
  @media screen and (min-width: 768px) {
    .skills-item-container {
      width: 30%;
    }
  }
  .skills-container {
    display: flex;
  }
  .skill-image {
    width: 30px;
    margin-right: 20px;
  }
  .skill-name {
    color: #ffffff;
    font-size: 16px;
    font-family: 'Roboto';
  }
  @media screen and (min-width: 768px) {
    .skill-image {
      width: 50px;
    }
    .skill-name {
      font-size: 18px;
    }
  }
  